body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-brand {
    font-size: 40px;
}

#nav-instructions {
    font-size: 30px;
}

#score-tracker {
    font-size: 40px;
}

#nfl-logo {
    height: 100px;
}

nav {
    background-color: #013368;
    color: white;
}
.jumbotron {
    background-image: url("https://us.123rf.com/450wm/efks/efks1610/efks161000007/66830512-american-football-field-and-grass.jpg?ver=6");
    background-size: cover;
    font-family: 'Merriweather', serif;
}
.wrapper {
  padding-top: 50px;
  background: #b5071e;
  height: 100%;
  /* display: flex; */
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 0px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  /* align-content: flex-start; */
  overflow: auto;
}

.card {
    background: #fff;
    border-radius: 2px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    display: -webkit-flex;
    display: flex;
}

.card-img-top {
    width: 324px;
    height: 205px;
}
